nav{
    height: 5rem;
    width: 100vw;
    background: var(--color-primary);
    display: grid;
    place-items: center;
    top: 0;
    left: 0;
    z-index: 99;
    position: fixed;
}

/* only show on small device */
.nav-toggle-btn{
    display: none;
}

.nav-container{
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;
}
.logo{
    width: 7rem;
    display: block;
}
.nav-links{
    display: flex;
    gap: 3.5rem;
    align-items: center;
}
.nav-links a{
    transition: var(--transition);

}
.active-nav{
    position: relative;
}
.active-nav::after{
    content: "";
    display: block;
    width: 1.2rem;
    height: 1.2rem;
    background: var(--color-primary);
    position: absolute;
    left: calc(50% - 0.6rem);
    transform: rotate(45deg);
    margin-top: 0.9rem;
}

/* MEDIA QUERIES ( medium and small screens) */

@media screen and (max-width: 1024px){
    .nav-toggle-btn{
        display: inline-block;
        background: transparent;
        font-size: 1.8rem;
        cursor: pointer;
    }
    .nav-toggle-btn svg{
        color: var(--color-gray-100);

    }
    .nav-links{
        position: absolute;
        top: 100%;
        right: 0;
        flex-direction: column;
        gap: 0;
        perspective: 400px;
    }
    .active-nav, .active-nav::after{
        display: none;
    }
    .nav-links li{
        height: 4rem;
        width: 100%;
        box-shadow: -2rem 2rem 5rem rgba(0, 0, 0, 0.5);
        animation: navAnimation 600ms ease forwards;
        transform: rotateX(90deg);
        opacity: 0;
        transform-origin: top;

    }
    .nav-links li :nth-child(2){
        animation-delay: 200ms;
    }
    .nav-links li :nth-child(3){
        animation-delay: 400ms;
    }
    .nav-links li :nth-child(4){
        animation-delay: 600ms;
    }
    .nav-links li :nth-child(5){
        animation-delay: 800ms;
    }
    .nav-links li :nth-child(6){
        animation-delay: 1000ms;
    }
    
    .nav-links li a{
        background-color: var(--color-primary);
        height: 100%;
        width: 100%;
        display: flex;
        align-items: center;
        padding: 1rem 5rem 1rem 3rem;
    }
    @keyframes navAnimation {
        to {
        transform: rotateX(0);
        opacity: 1;
    }

    }
    .show-Nav{
        display: flex;
    }
    .hide_Nav{
        display: none;
    }

    
}