.trainers-container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);

    gap: 4rem;
}
.trainers{
    padding: 1rem;
}
.trainer-img{
    border-radius: 0 2rem;
    overflow: hidden;
}
.trainers h3{
    margin-top: 2rem;
}
.trainers p{
margin-top: 0.5rem;
}
.trainers-socials{
    display: flex;
    gap: 1rem;
    justify-content: center;
    align-items: center;
    margin: 2rem 0;
    border-radius: 0.7rem !important;
    transition: var(--transition);

}
.trainers-socials a{
    font-size: 0.7rem;
    background: var(--color-gray-600);
    padding: 0.8rem;
    border-radius: 0.7rem;
    transition: var(--transition);
   cursor: pointer;
}

@media(max-width:1024px) {
    .trainers-container{
        grid-template-columns: 1fr 1fr;
        gap: 2.5rem;
    }
    
}
@media(max-width:600px) {

    .trainers-container{
        grid-template-columns: 1fr;
        gap: 2rem;
    }
    .trainers{
        width: 90%;
        margin-inline: auto;
    }
}