@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap');
*, *::before, *::after {
    margin: 0;
    padding: 0;
    border: 0;
    outline: 0;
    list-style: none;
    text-decoration: none;
    box-sizing: border-box;
    scroll-behavior: smooth;
}
:root{
    --primary-hue: 210;
    --gray-hue: 240;
    --color-primary:hsl(var(--primary-hue),100%, 50%);
    --color-primary-varient: hsl(var(--primary-hue),100%, 65%);
    --color-secondary :#ffb116;
    --color-gray-100: hsl(var(--primary-hue), 47%, 94%);
    --color-gray-200: hsl(var(--primary-hue), 26%, 75%);
    --color-gray-300: hsl(var(--primary-hue), 24%, 64%);
    --color-gray-400: hsl(var(--primary-hue), 16%, 41%);
    --color-gray-500: hsl(var(--primary-hue), 44%, 25%);
    --color-gray-600: #151335;
    --container-width-lg: 80%;
    --container-width-md: 90%;
    --transition:all 500ms ease;

}

/* General style */
body{
    font-family: 'Montserrat', sans-serif;
    color: var(--color-gray-200);
    line-height: 1.7rem;
    overflow-x: hidden;
    background: var(--color-gray-600) url('./images//bg_texture.png');
}
.container{
    width: var(--container-width-lg);
    max-width: 1920px;
    margin-inline: auto;

}
h1,h2,h3,h4,h5{
    line-height: 1.2rem;
    color: var(--color-gray-100) !important;
}

h1{
    font-size: 2.5rem;
    line-height: 4rem;
}
h2{
    font-size: 2rem;
}

a{
    color: var(--color-gray-100);
}
img{
    display: block;
    object-fit: cover;
    width: 100%;
}
a{
    color: var(--color-gray-100);

}

.btn{
    color: var(--color-gray-100);
    width: fit-content;
    margin: 0 auto;
    padding: 0.9rem 2rem;
    background: var(--color-primary);
    border-radius: 1.5rem;
    transition: var(--transition);
}
.btn:hover{
    background: var(--color-secondary);
    color: var(--color-gray-100);
}
.btn.lg{
    padding: 1.2rem 3rem;
    border-radius: 2rem;
    font-size: 1.1rem;
}
.btn.sm{
    padding: 0.4rem 1.2rem;
    font-size: 0.9rem;
}
section {
    margin-top: 10rem;
}
.section-head{
    display: flex;
    align-items: center;
    gap: 1rem;
}
.section-head span{
    background: var(--color-gray-500);
    padding: 0.7rem;
    border-radius: 1rem;
    color: var(--color-secondary);
    font-size: 1.5rem;
}
.card{
    background: #252359;
    border: 2px solid transparent;
    text-align: center;
    padding: 3rem 2rem;
    border-radius: 0 3rem 0 3rem;
    transition: var(--transition);
}
.card:hover{
    background: transparent;
    border-color: var(--color-gray-400);
    cursor: default;
}
.card span{
    width: 3rem;
    height: 3rem;
    background: var(--color-primary);
    color: var(--color-gray-100);
    font-size: 1.5rem;
    padding: 0.8rem;
    display: grid;
    place-items: center;
    margin-bottom: 1.5rem;
    margin-inline: auto;
    border-radius: 1rem;
    transition:  var(--transition);
}
.card:hover span{
    background: var(--color-secondary);
    color: var(--color-gray-600);
}
.card small{
    margin-top: 1rem;
    display: block;
    font-weight: 300;
}

header{
    margin-top: 5rem;
    height: 20rem;
    overflow: hidden;
    border-bottom: 2px solid var(--color-gray-400);
}
.header-container{
    width: 100%;
    height: 100%;
    position: relative;
    display: grid;
    place-items: center;
    background: black;
}
.header_container-bg{
    position: absolute;
}
.header_container-bg img{
    opacity: 0.5 !important;
}
.header-content{
    position: relative;
    width: 44%;
    margin: 5px auto;
    text-align: center;
    color: #7A7881;
}
.header-content h2{
    margin-bottom: 2rem;
}

    /* MEDIA QUERIES (MEDIUM DEVICE) */

    @media(max-width:1024px) {
        .container{
            width: var(--container-width-md);
        }
        h1{
            font-size: 1.7rem;
        }
        h2{
            font-size: 1.2rem;
        }
        .header-content{
            width: 75%;
        }
    }

    @media(max-width:600px) {
        
        h1{
            font-size: 2.2rem;
            line-height: 1.3;
        }
        section{
            margin-top: 7rem;
        }
        .header{
            height: fit-content;
        }
        .header-content{
            width: var(--container-width-md);
            padding: 3rem 0;
        }
        .header-content p{
            font-style: 0.85rem;
        }
    }