.main-header{
    width: 100vw;
    height: calc(100vh - 4rem) !important;
    display: grid;
    place-items: center;
    margin-top: 5.5rem;

}
.main_header-container{
    height: 100%;
    display: grid !important;
    grid-template-columns: 1fr 1fr !important;
    align-items: center;
    justify-content: space-between;
    gap: 6rem;
}
.main-header-left h4{
  margin-bottom: 1rem;
  color: var(--color-secondary)!important;
  font-weight: 400;
}
.main-header-left p{
    margin: 1rem 0 3.5rem;
    font-size: 1.1rem;
}
.main-header-right{
  display: grid;
  place-items: center;
  position: relative;
}
.main-header-circle{
    width: 25rem;
    height: 25rem;
    background: linear-gradient(75deg , var(--color-secondary),transparent);
    border-radius: 50%;
    position: absolute;
    /* filter: blur(15px); */
}
.main-header-image{
    position: relative;
}
.main-header-right:hover .main-header-circle{
    filter: blur(15px);
}


/* ==============Programms============= */

.programs{
    margin-top: 4rem;
}
.programs_wrapper    {

    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 3rem;
    margin-top: 4rem;

}

.programs_program a{
    margin-top: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
}
.programs_wrapper:hover a{
    background: var(--color-secondary);
    color: var(--color-gray-600);
}
/* =============VALUES============ */

.values-container{
    display: grid;
    grid-template-columns: 38% 50%;
    gap: 12%;
}
.values-img{
    filter: saturate(0.25);
    transition: var(--transition);
}
.values-img:hover{
    filter: saturate(0.25);
}
.values-right > p{
    margin: 1.5rem 0 5rem;
}
.values-wrapper{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 3.4rem 2.4rem;
}
.card.values-value{
    padding-top: 3.5rem;
    text-align: left;
    position: relative;
}
.card.values-value span{
    position: absolute;
    top: -1.5rem;
}

/* ===========FAQS ================ */

.faqs-wrapper{
    margin-top: 4rem;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1.5rem 5rem;
    align-items: flex-start;
}

.faq{
    background: #1D1C47;
    padding: 2rem;
    border-radius: 1rem;
    cursor: pointer;
}
.faq div{
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.faq-icon{
    background: transparent;
    color:  var(--color-gray-100);
    font-style: 1.5rem;
}
.faq p{
    margin-top: 1.5rem;
}

/* ============testimonials============= */
.testimonials{
    width: 90%;
    margin-inline: auto;
}
.testimonials-head{
    justify-content: center;
}
.card.testimonials{
    position: relative;
    text-align: left;
    margin-top: 5rem;
}
.testimonial-avatar{
    width: 4rem;
    height: 4rem;
    border-radius: 1.5rem;
    overflow: hidden;
    position: absolute;
    border: 2px solid transparent;
    top: -2rem;
    left: calc(50% -2rem);
    box-shadow: 0 1.5rem 2rem rgba(26, 26, 54, 0.4);
    transition: var(--transition);
}
.testimonials:hover .testimonial-avatar{
    border-radius: 50%;
    border-color: var(--color-gray-400);
}
.testimonial-quote{
    font-style: italic;
    margin: 1rem 0 2rem;
}

.card small .testimonial-title{
    margin-top: 0.3rem;
}
.testimonial_btn-container{
    width: fit-content;
    margin: 2.5rem auto 0;
    display: flex;
    align-items: center;
    gap: 1rem;
}
.testimonials-btn{
    background: transparent;
    cursor: pointer;
}
.testimonials-btn svg{
    fill: var(--color-primary);
    font-size: 1.2rem;
    width: 2rem;
    height: 2rem;
}

footer{
    background: var(--color-primary);
    margin-top: 7rem;
    padding-top: 7rem;
    font-size: 0.9rem;
    color: var(--color-gray-100);
}
.footer-container{
    display: grid;
    grid-template-columns: 26rem 1fr 1fr 1fr;
    gap: 6rem;

}
.footer-container article{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 0.7rem;
}
.footer-container article p{
    margin-top: 0.5rem;
}
.footer-container article h4{
    margin-bottom: 0.6rem;
}
.footer-socials{
    margin-top: 1.5rem;
    display: flex;
    gap: 1rem;
    align-items: center;
}
.footer-socials a{
    background: var(--color-gray-100);
    padding: 0.7rem 0.9rem;
    border: 1px solid transparent;
    border-radius: 20%;
    transition: var(--transition);
}
.footer-socials a svg{
    color: #1D1C47;
    font-size: 22px;
    margin-top: 5px;

}
.footer-socials a:hover{
    border-color: var(--color-gray-100);
    background: transparent;
}
.footer-copyright{
    color: var(--color-gray-100);
    text-align: center;
    padding: 1.5rem 0;
    border-top: 2px solid var(--color-primary-varient);
    margin-top: 5rem;
}
/* MEDIA QUERUES (MEDIUM SCREENS) */
@media(max-width: 1024px){
    
    .main-header{
        height: fit-content;
        width: fit-content !important;
        padding: 12rem 0;
    }
    .main_header-container{
        gap: 0 !important;
    }
    .main-header-circle{
        width: 16rem;
        height: 16rem;
    }


    .programs_wrapper{
        grid-template-columns: 1fr 1fr;
    }
    .values-container{
        grid-template-columns: 1fr;
        justify-content: center;
    }
    .values-wrapper{
        gap: 4rem 3rem;
    }
    .values-img{
        display: none;
    }
    .faqs-wrapper{
        grid-template-columns: 1fr;
    }
    .testimonials{
        width: 75%;
    }
    .footer-container{
        grid-template-columns: 1fr 1fr;
        gap: 4rem;
    }
}@media(max-width: 1024px){
    .main-header{
        height: 100vh;
        margin-top: 0;
        padding: 12rem 0;
    }
    .main-header-image,.main-header-circle{
        display: none;
    }
    .main_header-container{
        grid-template-columns: 1fr;
    }
    .programs_wrapper{
        grid-template-columns: 1fr;
        gap: 2rem;
        margin-top: 3rem;
    }
    .programs_program{
        width: 84%;
        margin: 0 auto;
    }
    .values-wrapper{
        grid-template-columns: 1fr;
        gap: 3rem;
    }
    .card.values-value{
        width: 84%;
        margin-inline: auto;
    }
    .faqs-wrapper{
        gap: 1rem;
        margin-top: 3rem;
    }
    .testimonials{
        width: 100%;
    }
    footer{
        margin-top: 7rem;
    }
    .footer-container{
        grid-template-columns: 1fr 1fr;
        gap: 3rem;
    }
    .footer-container article p{
        align-items: center !important;
    }
}