.plans_container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 3rem;
}
.plans h3{
    margin-bottom: 0.8rem;
}
.plans small{
    margin-bottom: 2rem;
}
.plans h1{
  color: var(--color-primary-varient ) !important;
  display: inline-block;
}
.plans h2{
    display: inline;
    color: gray !important;
}
.plans p.disabled{
    color: var(--color-gray);
}
.plans button{
    margin-top: 3rem;
    cursor: pointer;
}

@media(max-width:1024px){
    .plans_container{
        grid-template-columns: 1fr 1fr !important;
        gap: 2rem;
    }

}
@media(max-width:600px){
    .plans_container{
        grid-template-columns: 1fr;
    }
    .plans{
        width: 90%;
        margin-inline: auto;
    }
}